body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* Universal Styles */

html {
	font-size: 18px;
}

@media only screen and (max-width: 560px) {
	html {
		font-size: 15px;
	}
}

input:focus {
	outline: none;
}

/* Reusable Component - Image Container */

.image-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	overflow: hidden;
	background: #000;
}

.image-container img {
	width: 100%;
}

/* App Styles */

.App {
	font-family: 'Work Sans', sans-serif;
	text-align: center;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}

h1 {
	padding: 0.66rem 0;
	text-align: center;
	background-color: #cca353;
	font-family: Poppins, sans-serif;
	font-size: 2rem;
	color: #fff;
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 80px;
}

.App-header {
	background-color: #222;
	height: 150px;
	padding: 20px;
	color: white;
}

.App-title {
	font-size: 1.5em;
}

.App-intro {
	font-size: large;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.BusinessList {
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	margin: 4.4rem 10%;
	flex: 1 1;
}

.Business {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 14.66rem;
  margin: 0 0.5rem 2.3rem 0.5rem;
}

.Business .image-container {
  height: 14.66rem;
  margin-bottom: 1rem;
}

.Business h2 {
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.Business-information {
  display: flex;
  justify-content: space-between;
}

.Business-information p {
  font-size: 0.88rem;
  font-weight: 300;
  line-height: 1rem;
}

.Business-address {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Business-reviews {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: right;
}

.Business-reviews h3 {
  color: #f0c36c;
  font-weight: 600;
}

.Business-reviews .rating {
  font-size: 0.88rem;
  line-height: 1rem;
}

.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: url(/Ravenous/static/media/background_search_desktop.902df4c5.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 20rem;
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  margin-bottom: 2.22rem;
  color: #ffffff;
}

.SearchBar-sort-options li {
  cursor: pointer;
  width: 4.33rem;
  border-bottom: 1px solid #fff;
  padding: 0 2.58rem 0.33rem 2.58rem;
  line-height: 1.13;
  text-align: center;
  font-weight: 600;
  font-size: 0.83rem;
  transition: color 0.25s;
}

.SearchBar-sort-options li:hover {
  color: #d4cfcf;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-bottom: 1px solid #f0c36c;
  color: #f0c36c;
}

.SearchBar-fields {
  display: flex;
  justify-content: center;
  margin-bottom: 2.88rem;
}

.SearchBar-fields input {
  width: 21rem;
  padding: 0.66rem 1rem;
  margin-right: 2.22rem;
  border: 1px solid #fff;
  border-radius: 4px;
  font-size: 0.77rem;
  font-weight: 500;
}

.SearchBar-fields input:last-child {
  margin-right: 0;
}

.SearchBar-submit {
  text-align: center;
}

.SearchBar-submit button {
  border-radius: 4px;
  border: 0;
  padding: 0.72rem 1.7rem;
  background-color: #cca353;
  color: #ffffff;
  font-weight: 600;
  transition: background-color 0.5s;
}

.SearchBar-submit a:hover {
  cursor: pointer;
  background-color: #a7874b;
}

@media only screen and (max-width: 900px) {
  .SearchBar-fields {
    margin: 1rem;
  }
}

@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: url(/Ravenous/static/media/background_search_mobile.5d0905e6.jpg);
  }

  .SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .SearchBar-fields {
    flex-direction: column;
    align-items: center;
  }

  .SearchBar-fields input {
    margin-right: 0;
    margin-bottom: 0.86rem;
  }
}

.footer {

  background-color: #cca353;
  padding: 1rem 2rem;
  bottom: 0;
}

.footer .wrapper {
  max-width: 1800px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.footer h1,
.footer h2 {
  padding: 0.66rem;
  font-family: Poppins, sans-serif;
  font-size: 1.4rem;
  color: #fff;
}

.footer h2 {
  font-size: 1rem;
}

@media only screen and (max-width: 560px) {
  .footer .wrapper {
    max-width: 1800px;
    flex-direction: column;
    margin: 0 auto;
  }
}

